import React from 'react';
import styled from 'styled-components';

const OptionsContent = styled.div`
  color: var(--color-textPrimary);
  line-height: 1.6;
`;

const NavigationContainer = styled.div`
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
  margin-bottom: 24px;
  padding: 16px;
  background: linear-gradient(135deg, var(--color-backgroundSecondary) 0%, var(--color-surface) 100%);
  border-radius: 12px;
  border: 1px solid var(--color-border);
`;

const NavTitle = styled.h4`
  width: 100%;
  margin: 0 0 12px 0;
  color: var(--color-error);
  font-weight: 600;
`;

const NavButton = styled.button`
  background: linear-gradient(135deg, var(--color-error) 0%, var(--color-errorHover) 100%);
  color: var(--color-textInverse);
  border: none;
  padding: 6px 12px;
  border-radius: 8px;
  cursor: pointer;
  font-size: 12px;
  font-weight: 500;
  transition: all 0.3s ease;

  &:hover {
    transform: translateY(-2px);
    box-shadow: 0 4px 12px rgba(220, 53, 69, 0.3);
  }

  &:active {
    transform: translateY(0);
  }
`;

const OptionsSection = styled.div`
  margin-bottom: 24px;
  padding: 20px;
  background: linear-gradient(135deg, var(--color-backgroundElevated) 0%, var(--color-surface) 100%);
  border-radius: 12px;
  border-left: 4px solid var(--color-error);
`;

const SectionTitle = styled.h3`
  color: var(--color-error);
  margin-bottom: 12px;
  font-weight: 600;
  display: flex;
  align-items: center;
  gap: 8px;
`;

function OptionsModalContent() {
  const scrollToSection = (sectionId) => {
    const element = document.getElementById(sectionId);
    if (element) {
      element.scrollIntoView({ 
        behavior: 'smooth',
        block: 'start'
      });
    }
  };

  return (
    <OptionsContent>
      <NavigationContainer>
        <NavTitle> Navegacin Rpida:</NavTitle>
        <NavButton onClick={() => scrollToSection('options-projects')}>Proyectos</NavButton>
        <NavButton onClick={() => scrollToSection('options-save')}>Guardado</NavButton>
        <NavButton onClick={() => scrollToSection('options-files')}>Archivos</NavButton>
        <NavButton onClick={() => scrollToSection('options-script')}>Exportar Script</NavButton>
        <NavButton onClick={() => scrollToSection('options-export')}>Exportar Proyecto</NavButton>
        <NavButton onClick={() => scrollToSection('options-preview')}>Vista Previa</NavButton>
        <NavButton onClick={() => scrollToSection('options-variables')}>Variables</NavButton>
        <NavButton onClick={() => scrollToSection('options-conditions')}>Condiciones</NavButton>
        <NavButton onClick={() => scrollToSection('options-config')}>Configuracin</NavButton>
        <NavButton onClick={() => scrollToSection('options-tips')}>Consejos</NavButton>
      </NavigationContainer>

      <OptionsSection id="options-projects">
        <SectionTitle> Gestin de Proyectos</SectionTitle>
        <h4>Configuracin Bsica:</h4>
        <ul>
          <li><strong>Nombre del Proyecto:</strong> Identificador nico de tu novela visual</li>
          <li><strong>Descripcin:</strong> Breve resumen de la historia</li>
          <li><strong>Versin:</strong> Control de versiones de tu proyecto</li>
          <li><strong>Autor:</strong> Tu nombre o seudnimo</li>
        </ul>
        
        <h4>Configuracin Avanzada:</h4>
        <ul>
          <li><strong>Resolucin:</strong> Tamao de pantalla (recomendado: 1920x1080)</li>
          <li><strong>Idioma:</strong> Idioma principal del proyecto</li>
          <li><strong>Etiquetas:</strong> Categoras para organizar tu proyecto</li>
        </ul>
      </OptionsSection>

      <OptionsSection id="options-save">
        <SectionTitle> Sistema de Guardado</SectionTitle>
        <h4>Autoguardado:</h4>
        <ul>
          <li><strong>Frecuencia:</strong> El proyecto se guarda automticamente cada 30 segundos</li>
          <li><strong>Respaldo:</strong> Se mantienen mltiples versiones de respaldo</li>
          <li><strong>Indicador:</strong> cono en la esquina muestra el estado del guardado</li>
        </ul>
        
        <h4>Guardado Manual:</h4>
        <ul>
          <li><strong>Ctrl+S:</strong> Guarda inmediatamente los cambios</li>
          <li><strong>Exportar:</strong> Guarda una copia independiente del proyecto</li>
          <li><strong>Versiones:</strong> Puedes crear puntos de guardado especficos</li>
        </ul>
      </OptionsSection>

      <OptionsSection id="options-files">
        <SectionTitle> Gestin de Archivos</SectionTitle>
        <h4>Tipos de Archivo Soportados:</h4>
        <ul>
          <li><strong>Imgenes:</strong> PNG, JPG, JPEG, GIF, WEBP</li>
          <li><strong>Audio:</strong> MP3, WAV, OGG</li>
          <li><strong>Fuentes:</strong> TTF, OTF</li>
          <li><strong>Scripts:</strong> RenPy (.rpy)</li>
        </ul>
        
        <h4>Organizacin:</h4>
        <ul>
          <li><strong>Carpetas:</strong> Organiza recursos por categoras</li>
          <li><strong>Nombres:</strong> Usa nombres descriptivos y consistentes</li>
          <li><strong>Tamaos:</strong> Optimiza archivos para mejor rendimiento</li>
        </ul>
      </OptionsSection>

      <OptionsSection id="options-script">
        <SectionTitle> Exportar Script</SectionTitle>
        <h4>Formato RenPy:</h4>
        <ul>
          <li><strong>Archivo .rpy:</strong> Script principal compatible con Ren'Py</li>
          <li><strong>Estructura:</strong> Organizado en labels y escenas</li>
          <li><strong>Comentarios:</strong> Incluye comentarios para facilitar la edicin</li>
        </ul>
        
        <h4>Opciones de Exportacin:</h4>
        <ul>
          <li><strong>Solo Script:</strong> nicamente el archivo .rpy</li>
          <li><strong>Con Recursos:</strong> Script + imgenes y audio</li>
          <li><strong>Proyecto Completo:</strong> Estructura completa de Ren'Py</li>
        </ul>
      </OptionsSection>

      <OptionsSection id="options-export">
        <SectionTitle> Exportar Proyecto Completo</SectionTitle>
        <h4>Estructura del Proyecto:</h4>
        <ul>
          <li><strong>game/:</strong> Carpeta principal con scripts y recursos</li>
          <li><strong>images/:</strong> Todas las imgenes organizadas</li>
          <li><strong>audio/:</strong> Archivos de sonido y msica</li>
          <li><strong>gui/:</strong> Elementos de interfaz de usuario</li>
        </ul>
        
        <h4>Configuracin de Exportacin:</h4>
        <ul>
          <li><strong>Destino:</strong> Selecciona dnde guardar el proyecto</li>
          <li><strong>Compresin:</strong> Optimiza archivos para distribucin</li>
          <li><strong>Metadatos:</strong> Incluye informacin del proyecto</li>
        </ul>
      </OptionsSection>

      <OptionsSection id="options-preview">
        <SectionTitle> Vista Previa</SectionTitle>
        <h4>Configuracin de Vista Previa:</h4>
        <ul>
          <li><strong>Tiempo Real:</strong> Actualizacin automtica mientras editas</li>
          <li><strong>Resolucin:</strong> Ajusta el tamao de la vista previa</li>
          <li><strong>Modo Pantalla Completa:</strong> Ver en tamao real</li>
        </ul>
        
        <h4>Controles:</h4>
        <ul>
          <li><strong>Play/Pause:</strong> Controla la reproduccin de la escena</li>
          <li><strong>Navegacin:</strong> Avanza o retrocede entre dilogos</li>
          <li><strong>Velocidad:</strong> Ajusta la velocidad de texto</li>
        </ul>
      </OptionsSection>

      <OptionsSection id="options-variables">
        <SectionTitle> Sistema de Variables</SectionTitle>
        <h4>Tipos de Variables:</h4>
        <ul>
          <li><strong>Booleanas:</strong> Verdadero/Falso (True/False)</li>
          <li><strong>Numricas:</strong> Enteros y decimales</li>
          <li><strong>Texto:</strong> Cadenas de caracteres</li>
          <li><strong>Listas:</strong> Colecciones de valores</li>
        </ul>
        
        <h4>Uso de Variables:</h4>
        <ul>
          <li><strong>Decisiones:</strong> Controla qu opciones estn disponibles</li>
          <li><strong>Rutas:</strong> Determina el flujo de la historia</li>
          <li><strong>Estadsticas:</strong> Puntos, relaciones, inventario</li>
        </ul>
      </OptionsSection>

      <OptionsSection id="options-conditions">
        <SectionTitle> Sistema de Condiciones</SectionTitle>
        <h4>Tipos de Condiciones:</h4>
        <ul>
          <li><strong>Simples:</strong> if variable == valor</li>
          <li><strong>Compuestas:</strong> Mltiples condiciones con AND/OR</li>
          <li><strong>Rangos:</strong> variable &gt; X y variable &lt; Y</li>
        </ul>
        
        <h4>Aplicaciones:</h4>
        <ul>
          <li><strong>Dilogos Condicionales:</strong> Texto que aparece segn el estado</li>
          <li><strong>Opciones Dinmicas:</strong> Decisiones que cambian</li>
          <li><strong>Finales Mltiples:</strong> Diferentes conclusiones</li>
        </ul>
      </OptionsSection>

      <OptionsSection id="options-config">
        <SectionTitle> Configuracin Avanzada</SectionTitle>
        <h4>Interfaz:</h4>
        <ul>
          <li><strong>Tema:</strong> Claro u oscuro</li>
          <li><strong>Idioma:</strong> Idioma de la interfaz</li>
          <li><strong>Atajos:</strong> Personaliza combinaciones de teclas</li>
        </ul>
        
        <h4>Rendimiento:</h4>
        <ul>
          <li><strong>Autoguardado:</strong> Frecuencia de guardado automtico</li>
          <li><strong>Vista Previa:</strong> Calidad y frecuencia de actualizacin</li>
          <li><strong>Cach:</strong> Gestin de archivos temporales</li>
        </ul>
      </OptionsSection>

      <OptionsSection id="options-tips">
        <SectionTitle> Consejos de Configuracin</SectionTitle>
        <h4>Mejores Prcticas:</h4>
        <ul>
          <li><strong>Configuracin Inicial:</strong> Dedica tiempo a configurar bien tu proyecto</li>
          <li><strong>Respaldos:</strong> Exporta regularmente copias de seguridad</li>
          <li><strong>Organizacin:</strong> Mantn una estructura de carpetas clara</li>
          <li><strong>Pruebas:</strong> Verifica regularmente la vista previa</li>
        </ul>
        
        <h4>Optimizacin:</h4>
        <ul>
          <li><strong>Recursos:</strong> Comprime imgenes grandes</li>
          <li><strong>Audio:</strong> Usa formatos eficientes como OGG</li>
          <li><strong>Variables:</strong> No crees variables innecesarias</li>
          <li><strong>Script:</strong> Comenta tu cdigo para futuras referencias</li>
        </ul>
      </OptionsSection>
    </OptionsContent>
  );
}

export default OptionsModalContent;
