// Etiquetas de categoras
export const CATEGORY_LABELS = {
  core: ' Elementos Bsicos',
  characters: ' Personajes',
  interaction: ' Interaccin',
  flow: ' Flujo',
  logic: ' Lgica',
  media: ' Multimedia',
  effects: ' Efectos',
  special: ' Elementos Especiales'
};

// Agrupar por categoras
export const getElementTypesByCategory = () => {
  return ELEMENT_TYPES_CONFIG.reduce((acc, element) => {
    if (!acc[element.category]) {
      acc[element.category] = [];
    }
    acc[element.category].push(element);
    return acc;
  }, {});
};

// Funciones utilitarias
export const getElementTypeConfig = (type) => {
  return ELEMENT_TYPES_CONFIG.find(config => config.type === type);
};

export const getElementTypeLabel = (type) => {
  const config = getElementTypeConfig(type);
  return config ? config.label : type;
};

export const getElementTypeIcon = (type) => {
  const config = getElementTypeConfig(type);
  return config ? config.icon : '';
};

export const getElementTypeColor = (type) => {
  const config = getElementTypeConfig(type);
  return config ? config.color : '#4a9eff';
};

export const getElementTypeDescription = (type) => {
  const config = getElementTypeConfig(type);
  return config ? config.description : 'Sin descripcin';
};




